<template>
    <div class="mb-4 mb-lg-0 col col-md-6 col-lg-4">
        <div class="card d-flex flex-column">
            <img class="card-img-top" :src="imagePath" alt="Card image cap">
            <div class="card-body d-flex flex-column">
                <h5 class="card-title">
                    <slot name="title">Card title</slot>
                </h5>
                <p class="card-text">
                    <slot name="text">Card text</slot>
                </p>
                <div class="d-flex mt-auto flex-column align-items-center">
                    <router-link class="btn btn-primary" v-if="internal" :to="routerLink">
                        <slot name="button">Button</slot>
                    </router-link>
                    <a v-if="!internal" :href="routerLink" class="btn btn-primary">
                        <slot name="button">Button</slot>
                    </a>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
    export default {
        name: "Card",
        props: {
            image: {
                required: true,
                type: String
            },
            internal: {
                type: Boolean,
                default: false
            },
            routerLink: {
                type: String,
                required: true
            }
        },
        computed: {
            imagePath() {
                return require(`@/assets/img/${this.image}`)
            }
        }
    }
</script>

<style lang="scss" scoped>
    .card {
        height: 100%;
    }

    .card-img-top {
        height: 48%;
        object-fit: cover;
    }
</style>